dth: 100vw;\n  }\n}\n\n.entry-content-asset {\n  position: relative;\n  padding-bottom: 56.25%;\n  padding-top: 30px;\n  height: 0;\n  overflow: hidden;\n\n  iframe {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n  }\n}\n\n/** Captions */\n\n// TODO: .wp-caption {}\n// TODO: .wp-caption img {}\n// TODO: .wp-caption-text {}\n","// Top Nav\n.topbar-wrapper {\n  background: $color-slate;\n\n  .rtp-logo {\n    height: 25px;\n\n    @media #{$small-and-down} {\n      height: 20px;\n    }\n  }\n\n  .topbar {\n    height: 40px;\n  }\n\n  .topbar-menu-wrapper {\n    height: 100%;\n  }\n\n  .topbar-menu {\n    height: 100%;\n\n    ul {\n      height: 100%;\n    }\n\n    li {\n      height: 100%;\n      padding: 5px 15px;\n\n      a {\n        color: $color-white;\n        text-decoration: none;\n        text-transform: uppercase;\n        font-size: 0.7rem;\n        font-weight: normal;\n      }\n    }\n  }\n  // Active the current site item\n  .current-site {\n    background: $color-white;\n\n    a {\n      color: $color-slate !important;\n    }\n  }\n}\n",".pagination {\n  background: none;\n  text-align: center;\n\n  @extend .z-depth-0;\n\n  ul li {\n    padding: 0;\n    height: auto;\n\n    &::before {\n      display: none;\n    }\n\n    &.gap {\n      &.before::before {\n        display: inline-block;\n        position: relative;\n        content: '\\2026';\n        color: $color-black;\n        margin-right: 6px;\n        left: 0;\n      }\n\n      &.after::after {\n        display: inline-block;\n        position: relative;\n        content: '\\2026';\n        color: $color-black;\n        margin-right: 6px;\n        left: 0;\n      }\n    }\n  }\n\n  a, span {\n    color: $color-white !important;\n    display: inline-block;\n    min-width: 40px;\n    padding: 0 15px !important;\n    height: 40px;\n    line-height: 1.7em !important;\n    font-size: 1.2em;\n    font-weight: normal;\n    text-decoration: none;\n\n    &:hover, &